      }
      const seeDetail = (data) => push({name: "weightHistoryDetail", params: {data: JSON.stringify(data)}});
      const formatData = (time) => moment(time).format("YYYY-MM-DD");

      return {
        selectTimeCall,
        tabs,
        formatData,
        option,
        seeDetail,
        type
      }
    },
    components: {
      selectTime,
      DataValueItem,
      DataModal,
      SimpleHeader,
      vTabs: Tabs,
      vCell: Cell,
